	'hh7';
	margin: auto;
	grid-area: models;
	
   /* border: 2px solid black;  */
}

.s3-models div{
	font-size: 2rem;
	margin-top: 4rem;
	text-align: center;
	padding: 0;
}

.s3-models div:nth-child(-n+2){
	margin-top:0;
}

