<template>
  <div class="section s4" data-scroll-section>
    <div class="s4-img" data-scroll data-scroll-speed="0.8" v-on:mouseover="mouseOn" v-on:mouseleave="mouseOff">
      <div class="s4-imginner"></div>
    </div>
    <div class="s4-txt">
      <div class="t1">Women</div>
      <div class="t2">Not every body could be a model</div>
      <div class="t3">
        <Continue section="s4"/>
      </div>
    </div>
  </div>
</template>

<script>
import gsap from 'gsap';
import Continue from '../Continue';

export default {

  name: "SectionFour",
  components: { 
    Continue,


  },
  methods: {
    mouseOn: function(){
      
          gsap
          .timeline({defaults: {duration: 1, ease: 'expo'}})
          .to(".s4-img", {scale: 0.95})
          .to(".s4-imginner", {scale: 1.15, stagger: {from: 'center'}},0)
      
    },
    mouseOff: function(){
      
          gsap
          .timeline({defaults: {duration: 1, ease: 'expo'}})
          .to(".s4-img", {scale: 1})
          .to(".s4-imginner", {scale: 1, stagger: {from: 'center'}},0)
      
    },
    mouseOnContinue: function(){
      
          gsap
          .timeline({defaults: {duration: 1, ease: 'expo'}})
          .to(".continue", {rotate: 25})
      
    },
    mouseOffContinue: function(){
      
          gsap
          .timeline({defaults: {duration: 1, ease: 'expo'}})
          .to(".continue", {rotate: 0})
      
    }
  }
};
</script>

