<template>

     <div class="obj-button" v-on:mouseover="mouseOnContinue" v-on:mouseleave="mouseOffContinue">
          <div class="continue">
            <img src="../assets/svg/continue.svg" />
          </div>
          <div class="arrow">
            <div>
              <img src="../assets/svg/arrow.svg" />
            </div>
          </div>
        </div>

</template>

<script>

import gsap from "gsap";

export default {
  name: 'Continue',
  props: ['section'],
    methods: {
    mouseOnContinue: function(){
      
          gsap
          .timeline({defaults: {duration: 1, ease: 'expo'}})
          .to("."+this.section+" .continue", {rotate: 25})
      
    },
    mouseOffContinue: function(){
      
          gsap
          .timeline({defaults: {duration: 1, ease: 'expo'}})
          .to("."+this.section+" .continue", {rotate: 0})
      
    }
  },
}

</script>

<style scoped>
   .obj-button {
    display: grid;
    grid-template-areas: "button";
    width: 20vw;
    max-width: 120px;
    margin: auto;
    cursor: pointer;
  }
  .obj-button .continue {
    grid-area: button;
  }
  .obj-button .arrow {
    grid-area: button;
    width: 45%;
    margin: auto;
  }

</style>
